.modal-container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
}

.modal-content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;
  max-width: 800px;
  height: 80%;
  max-height: 600px;
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
  overflow: auto;
}

.modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}

.modal-image {
  width: 100%;
  height: auto;
  margin-bottom: 20px;
}

@media screen and (max-width: 768px) {
  .modal-content {
    width: 90%;
    height: 90%;
  }
}
